.all {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    transform: scale(0);
}

.all.active {
    transform: scale(1);
    
}

.flex {
    padding: 20px;
    border-radius: 10px;
    background-color: white;
}